	<div class="growthFileReport">
		<DownloadProgress :totalPages="totalPages" :currentPage="currentPage" v-show="isDownloading"></DownloadProgress>
		<div class="pdf" id="pdfDom" style="background-color:#E8E8E8;">
			<pageCover :name="name" :reportNum="reportNum" :updatedAt="updatedAt"></pageCover>
		</div>
		<div class="get_pdf">
			<div class="inner">
				<div class="btn_pdf" v-on:click="isDownloading = true;getPdf(htmlTitle);">下载报告</div>
			</div>
		</div>




		<div class="pdfs" style="background-color:#E8E8E8;" v-show="false">
<!-- 			<div class="page_shadow">
				<div class="page pdf-page">
					<div class="page_content" v-if="firstInterview && firstInterview.constructor == Object">
						<h5 class="header" id="fangtan">
							<span></span>初步访谈
						</h5>
						<div class="content result" id="catalog_3">
							<p class="report_time">访谈时间：{{firstInterview.date}}</p>
							<div class="report_mained">
								<div class="mained_block"></div>
								<div class="box">
									<p class="title">一般资料</p>
									<p class="detail">{{firstInterview.generalInformation}}</p>
								</div>
								<div class="box">
									<p class="title">个人陈述</p>
									<p class="detail">{{firstInterview.personalStatement}}</p>
								</div>
								<div class="box">
									<p class="title">咨询师观察</p>
									<p class="detail">{{firstInterview.consultantObservation}}</p>
								</div>
								<div class="box">
									<p class="title">家长反映</p>
									<p class="detail">{{firstInterview.parentsReflect}}</p>
								</div>
								<div class="box">
									<p class="title">咨询师总结</p>
									<p class="detail">{{firstInterview.consultantSummary}}</p>
								</div>
							</div>
						</div>
	                </div>
				</div>
			</div> -->
			<pageList></pageList>
			<!-- <pageMessage1></pageMessage1> -->
		</div>
	</div>
</template>
<script>
import DownloadProgress from '../../components/DownloadProgress.vue';
import pageCover from './components/pageCover.vue';//封面
import pageList from './components/pageList.vue';//目录
import pageMessage1 from './components/pageMessage1.vue';//基本信息
export default {
	components: { DownloadProgress, pageCover, pageList, pageMessage1},
    props: [],
    data() {
        return {
    	    isDownloading: false, //是否正在下载
            totalPages: 1, //总页数
            currentPage: 0,//当前页
        	// isDownlondPdf: true
        	reportData: null,
        	name: '',
        	reportNum: '',
        	updatedAt: '',
        }
    },
    computed: {

    },
    watch: {
    	reportData() {
			this.$nextTick(() => {
				this.countPage();
			})
    	}
    },
    created() {
    	this.viewReport();
    },
    mounted() {
    },
    methods: {
        scrollToView(id) {
            document.getElementById(id).scrollIntoView({
                block: 'start',
                inline: 'nearest',
                behavior: 'smooth'
            })
        },
        // 查看成长档案
        viewReport() {
            var token = window.sessionStorage.getItem('ymtxToken');
            var _this = this;
            this.$ajax.post(this.G_uri + '/careerGrowthReport/viewReport', {
            	id: this.$route.query.id
                },{
                    headers:{
                        token: token
                    }
                })
                // 监听数据返回
                .then((res)=> {
                	let data = res.data.data;
	                if(res.data.code == 2000) {
	                    this.name = data.name;
	                    this.reportNum = data.num;
	                    this.updatedAt = data.date;
	                    this.reportData = data;
	                }
                })
                .catch(function(error) {
                    console.log(error)
                })
        },

        countPage() {
        	let html1 = '<div class="page_shadow"><div class="page pdf-page">';
        	let html2 = '</div></div>';
        	let allHtml = '<div class="page_shadow"><div class="page pdf-page"><p class="big_page_number">1</p>';
        	// let allHtml = '<div class="page_shadow"><div class="page pdf-page">';
        	let pageNumber = 1;
        	let height = 1200;
        	let page_content_height = 0;
        	$('.page_content').each(function() {
        		// let nowHtml = $(this).outerHTML();
        		let nowHtml = $(this).prop('outerHTML')
        		let nowHeight = $(this).height();
        		page_content_height += nowHeight;
        		if(page_content_height <= height) {
        			allHtml = allHtml + nowHtml;
        		}else {
        			pageNumber ++;
        			let elPageNumber = '<p class="big_page_number">' + pageNumber + '</p>';
        			page_content_height = nowHeight
        			allHtml = allHtml + html2 + html1 +  nowHtml + elPageNumber;
        		}
        	})
        	allHtml  = allHtml + html2;
        	$('.pdf').append(allHtml)
        }
    }
}
</script>
<!-- <style type="text/css" lang='less' scoped>
